import React, { Component } from 'react'
import ZombieCard from "./ZombieCard";
import './static/ZombiePreview.css';
import MyWeb3 from './MyWeb3'
import {
    BrowserRouter as 
    Route,
    Link
  } from "react-router-dom"
import Page from "./Page";
import ComAlert from "./ComAlert";
import homebg from "./static/homebg.png";

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
             zombieCount:"",
             zombies:[],
             toArr: [],
             ToItem: ()=>{return(<div></div>)},
             flag: false,
             NoteContent:()=>{
                return(
                    <div className="note">
                         <p>
                            基础操作说明：
                        </p>
                        <p>
                            1. 玩家需提前连接钱包并设置“跳伞”条件。
                        </p>
                        <p>
                            2. 跳伞时，用户的部分资金将被冻结。
                        </p>
                        <p>
                            3. “跳伞”后，玩家将随机降落在一块土地上。根据跳伞条件，自动完成土地购买或过路费交纳。
                        </p>
                        <p>
                            4.根据实际支付金额，退回剩余冻结资金。
                        </p>
                        <p>
                            5.若连续24小时没有玩家参与游戏，土地等级最高的前三名玩家将按照50%、30%、20%的比例瓜分奖金。
                        </p>
                    </div>
                    
                )
            },
        }
    }
        
    componentDidMount(){
        let that = this
        let ethereum = window.ethereum
        if (typeof ethereum !== 'undefined' || (typeof window.web3 !== 'undefined')) {
            MyWeb3.init().then(function(res){
                that.zombieArmy()
            })
        }else {
            alert('You have to install MetaMask !')
        }
    }
    zombieArmy(){
        let that = this
        MyWeb3.zombieCount().then(function(result){
            if(result > 0){
                for(let i=0;i<result;i++){
                    MyWeb3.zombies(i).then(function (result) {
                        let _zombies = that.state.zombies
                        result.zombieId = i
                        _zombies.push(result);
                        that.setState({zombies:_zombies})
                    })
                }
            }
        })
    }

    toEvent(data) {
        console.log(data)
        this.setState({flag:true})
        this.setState({ToItem : () =>{
            return(
                <div>
                    <div className="item-note">
                    <p>
                        土地等级：{data.level}
                    </p>
                    <p>
                        TOKEN地址：{data.level}
                    </p>
                    <p>
                        拥有者：{data.name}
                    </p>
                </div>
                <div className="confim" onClick={this.confimEvent.bind(this)}>
                    确定
                </div>
            </div>)
            }
        })
    }

    confimEvent() {
        this.setState({flag:false})
    }

    componentWillUnmount() {
        this.setState = (state, callback) => {
            return
        }
    }
    render() { 
        let NoteContent = this.state.NoteContent
        let ToItem = this.state.ToItem
        let Flag = this.state.flag
        if(this.state.zombies.length>0) {
            return ( 
                <div className="cards">
                    <img src={homebg}/>
                    <div className="img-box">
                        <div className="to-item-waper master-item">
                            <div className="to-item-befor">
                            </div>
                            <div className="to-item-last">
                                
                            </div>
                            <div className="bonus">
                                奖金池：
                                <span>
                                    2123123123.212312ETH
                                </span>
                            </div>
                        </div>
                        {this.state.zombies.map((item,index)=>{
                            var name = item.name
                            const level = item.level
                            const data = {...item}
                            return(
                                <div className="to-item-waper" key={item.zombieId}>
                                    <div className="to-item-befor">
                                    </div>
                                    <div className="to-item-last" onClick={this.toEvent.bind(this,data)}>
                                        LV {level}
                                    </div>
                                </div>
                            )
                        })}
                        <NoteContent></NoteContent>
                    </div>
                    <ComAlert flag={Flag}>
                        <ToItem></ToItem>
                    </ComAlert>
                    <Route path="*" component={Page}></Route>
                </div> 
            )
        }else{
            return ( <div></div>)
        }
    }
}
 
export default Home;